import React from 'react';
import connect from '@vkontakte/vk-bridge';
import { View, Alert, Spinner, setActivePanel, ScreenSpinner } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';
import Home from './panels/Home';
import Persik from './panels/Persik';
import './panels/assets/css/style.css';
import mongoose from 'mongoose';

mongoose.connect('mongodb+srv://Anakin400001:Karalo2001@cluster0.pv5dx.mongodb.net/base?retryWrites=true&w=majority', { useUnifiedTopology: true, retryWrites: true, useCreateIndex: true, useNewUrlParser: true, useFindAndModify: false })

var schema = mongoose.Schema({ balance: String });
var User = mongoose.model('User', schema);

class App extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			isBtnActive: false,
			activePanel: 'home',
            fetchedUser: null,
            popout: <ScreenSpinner size='large' />,
		};
		this.reklama = this.reklama.bind(this);
        this.openDefault = this.openDefault.bind(this);
        this.closePopout = this.closePopout.bind(this);
    }

    openDefault(title, msg, actions) {
        this.setState({
            popout:
                <Alert
                    actions={[actions]}
                    onClose={this.closePopout}
                >   
                    <h2>{title}</h2>
                    <p>{msg}</p>
                </Alert>
        });
    }

    closePopout() {
        this.setState({ popout: null });
    }

	componentDidMount() {
		connect.subscribe((e) => {
			switch (e.detail.type) {
				case 'VKWebAppGetUserInfoResult':
                    this.setState({ fetchedUser: e.detail.data });
                    this.setState({ popout: null });
					break;
					case 'VKWebAppShowWallPostBoxResult':

  					break;
				default:
				
			}
		});
		connect.send('VKWebAppGetUserInfo', {});
		const user = new User({
			balance: 0.00,
		})
		user.save(function (err, user) {
			if (err) throw err
			console.log(user)
		})
	}

    reklama() {
		document.getElementById('onads').innerHTML = ''
    }
	
    onBlur1 = () => {
    	 this.setState({ isBtnActive: false });
    }

    dark = () => { 
        document.body.setAttribute( 'scheme','client_dark' ); 
    }
    
    light = () => { 
        document.body.setAttribute( 'scheme','client_light' ); 
    }

    go = (e) => {
        this.setState({ activePanel: e.currentTarget.dataset.to })
    }
    
	render() {
		return (
			<View popout={this.state.popout} activePanel={this.state.activePanel}>
                <Home id="home" dark={this.dark} light={this.light} fetchedUser={this.state.fetchedUser} go={this.go} reklama={this.reklama} />
                <Persik id="persik" go={this.go} fetchedUser={this.state.fetchedUser} />
			</View>
		);
	}
}

export default App;
