import React from 'react';
import PropTypes from 'prop-types';
import connect from '@vkontakte/vkui-connect';
import Icon24Flash from '@vkontakte/icons/dist/24/flash';
import Icon24Mention from '@vkontakte/icons/dist/24/mention';
import Icon24User from '@vkontakte/icons/dist/24/user';
import Icon24Coins from '@vkontakte/icons/dist/24/coins';
import Icon24Favorite from '@vkontakte/icons/dist/24/favorite';
import Icon24Bug from '@vkontakte/icons/dist/24/bug';
import Icon24Filter from '@vkontakte/icons/dist/24/filter';
import Icon24Note from '@vkontakte/icons/dist/24/note';
import Icon24Poll from '@vkontakte/icons/dist/24/poll';
import Icon24Game from '@vkontakte/icons/dist/24/game';
import Icon24Home from '@vkontakte/icons/dist/24/home';
import Icon24Market from '@vkontakte/icons/dist/24/market';
import Icon24Services from '@vkontakte/icons/dist/24/services';
import Icon24BrowserForward from '@vkontakte/icons/dist/24/settings';
import Icon28MoneySendOutline from '@vkontakte/icons/dist/28/money_send_outline';
import Icon28MoneyTransfer from '@vkontakte/icons/dist/28/money_transfer';
import Icon36MoneyCircleOutline from '@vkontakte/icons/dist/36/money_circle_outline';
import Icon44GiftOutline from '@vkontakte/icons/dist/44/gift_outline';
import './assets/css/bootstrap.css';
import './assets/css/style.css';
import '@vkontakte/vkui/dist/vkui.css';
import { Panel, List, Button, Group, Div, Avatar, Gallery, Cell, Alert } from '@vkontakte/vkui';

const Home = ({ id, fetchedUser, go, dark, light, reklama }) => (
    <Panel id="home">
	{fetchedUser && <div id="headerlogo"><img class="Avatarka" src={fetchedUser.photo_200}></img>
	</div>}
	    <div id="balance">
	      <div class="BalanceBox">
                        <center class="BalanceText">БАЛАНС</center>
                        <center class="BalanceNumber">0.00 </center></div></div>
	<div class="MenuButtons"><div class="MenuButtons__button" onClick={go} data-to="top">
		<h4> </h4><center className='MenuButtons__icon'>
            <Icon28MoneySendOutline />
            </center>
        <p>Пополнить</p>
    </div>
	<div class="MenuButtons__button" onClick={go} data-to="top">
		<h4> </h4><center className='MenuButtons__icon'>
            <Icon28MoneyTransfer />
            </center>
        <p>Перевести</p>
    </div></div>
	<div class="MenuButtons"><div id="Save" className="MenuButtons__button">
		<h4> </h4><center className='MenuButtons__icon'>
            <Icon44GiftOutline />
            </center>
        <p>Бонус за рекламу(+0.05)</p>
    </div></div>
	<div>ЛОГИ:</div>
	<div id="logs"></div>
    </Panel>
);



Home.propTypes = {
    id: PropTypes.string.isRequired,
    go: PropTypes.func.isRequired,
    fetchedUser: PropTypes.shape({
        photo_200: PropTypes.string,
        first_name: PropTypes.string,
        last_name: PropTypes.string,
        city: PropTypes.shape({
            title: PropTypes.string,
        }),
    }),
};
export default Home;
